body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  min-height: 100vh;
  background-color: #fafafa;
  overflow-y: hidden;
}

.Navlink{
  color: #fff
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  text-align: center;
  height: 90vh;
  overflow-y: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/** KEYFRAME **/
@-webkit-keyframes wave-flag-front {
  from {
    width: 90px; }
  to {
    width: 160px; } }
@keyframes wave-flag-front {
  from {
    width: 90px; }
  to {
    width: 160px; } }

@-webkit-keyframes wave-flag-back {
  from {
    left: 42px;
    width: 140px; }
  to {
    left: 112px;
    width: 70px; } }

@keyframes wave-flag-back {
  from {
    left: 42px;
    width: 140px; }
  to {
    left: 112px;
    width: 70px; } }

/** BASIC **/
.center {
  height: 200px;
  width: 245px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -180px;
  margin-left: -122px; }

/** FLAG **/
.flag-container {
  height: 200px;
  position: relative; }

.pole {
  background: #e0e0e0;
  display: block;
  height: 180px;
  position: relative;
  -webkit-transform: skew(20deg, 0deg);
          transform: skew(20deg, 0deg);
  width: 10px; }

.knob {
  background: #e0e0e0;
  border-radius: 50%;
  display: block;
  height: 18px;
  left: -47px;
  position: absolute;
  top: -18px;
  width: 18px; }

.flag {
  background: #f2f2f2;
  display: block;
  position: absolute;
  -webkit-transform: skew(20deg, 0deg);
          transform: skew(20deg, 0deg); }

.flag.front {
  left: -17px;
  height: 110px;
  overflow: hidden;
  top: 0;
  width: 90px;
  z-index: 10;
  -webkit-animation: wave-flag-front 0.7s ease-in-out infinite alternate;
          animation: wave-flag-front 0.7s ease-in-out infinite alternate; }
  .flag.front .rect {
    background: #b71c1c;
    display: block;
    height: 60px;
    position: absolute;
    width: 200px;
    z-index: 100; }

.flag.back {
  background: #e0e0e0;
  left: 42px;
  height: 90px;
  top: 45px;
  width: 140px;
  z-index: 5;
  overflow: hidden;
  -webkit-animation: wave-flag-back 0.7s ease-in-out infinite alternate;
          animation: wave-flag-back 0.7s ease-in-out infinite alternate; }
  .flag.back .rect2 {
    background: #7f0000;
    display: block;
    height: 40px;
    position: absolute;
    width: 140px;
    z-index: 200; }
  .flag.back .shadow {
    border-color: #cccccc transparent transparent transparent;
    border-style: solid;
    border-width: 35px 45px 0 0;
    bottom: 0;
    height: 0;
    position: absolute;
    width: 0;
    z-index: 15;
    -webkit-transform: translate(-65px);
            transform: translate(-65px); }

